// WebGL 2.0 API


class GL2 {
  constructor(gl) {
    this.gl = gl;
  }

  // shaderSrctypeWebGLShader
  createShader(shaderSrc, type) {
    // WebGL2RenderingContext
    const gl = this.gl;
    // WebGLShader
    const shader = gl.createShader(type);
    // WebGLShader
    gl.shaderSource(shader, shaderSrc);
    // 
    gl.compileShader(shader);
    // 
    if (!gl.getShaderParameter(shader, gl.COMPILE_STATUS)) {
      throw new Error(gl.getShaderInfoLog(shader));
    }
    return shader;
  }

  // 
  createFrameBuffer(width, height){
    const gl = this.gl;
    // 
    const frameBuffer = gl.createFramebuffer();
    
    // WebGL
    gl.bindFramebuffer(gl.FRAMEBUFFER, frameBuffer);
    
    // 
    const depthRenderBuffer = gl.createRenderbuffer();
    gl.bindRenderbuffer(gl.RENDERBUFFER, depthRenderBuffer);
    
    // 
    gl.renderbufferStorage(gl.RENDERBUFFER, gl.DEPTH_COMPONENT16, width, height);
    
    // 
    gl.framebufferRenderbuffer(gl.FRAMEBUFFER, gl.DEPTH_ATTACHMENT, gl.RENDERBUFFER, depthRenderBuffer);
    
    // 
    const fTexture = gl.createTexture();
    
    // 
    gl.bindTexture(gl.TEXTURE_2D, fTexture);
    
    // 
    gl.texImage2D(gl.TEXTURE_2D, 0, gl.RGBA, width, height, 0, gl.RGBA, gl.UNSIGNED_BYTE, null);
    
    // 
    gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MAG_FILTER, gl.LINEAR);
    gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MIN_FILTER, gl.LINEAR);
    
    // 
    gl.framebufferTexture2D(gl.FRAMEBUFFER, gl.COLOR_ATTACHMENT0, gl.TEXTURE_2D, fTexture, 0);
    
    // 
    gl.bindTexture(gl.TEXTURE_2D, null);
    gl.bindRenderbuffer(gl.RENDERBUFFER, null);
    gl.bindFramebuffer(gl.FRAMEBUFFER, null);
    
    // 
    return {frameBuffer : frameBuffer, depthBuffer : depthRenderBuffer, texture : fTexture};
  }

  // 2WebGLShader
  // WebGLProgram
  createProgram(vs, fs) {

    // WebGL2RenderingContext
    const gl = this.gl;

    if(typeof vs == 'string'){
      vs = this.createShader(vs,gl.VERTEX_SHADER);
    }

    if(typeof fs == 'string'){
      fs = this.createShader(fs,gl.FRAGMENT_SHADER);
    }

    // WebGLProgram
    const program = gl.createProgram();
    // 
    gl.attachShader(program, vs);
    // 
    gl.attachShader(program, fs);
    // 
    gl.linkProgram(program);
    // 
    if (!gl.getProgramParameter(program, gl.LINK_STATUS)) {
      throw new Error(gl.getProgramInfoLog(program));
    }
    return program;
  }

  loadImage(url) {
    return new Promise((resolve,reject)=>{
      var image = new Image();
      try {
        image.src = url;
        image.onload = ()=>{
          resolve(image);
        };
      } catch (e){
        reject(e);
      }
     });
  }

  createTexture(url){

  }
}

export default GL2;
